import React from 'react'

export default function Selected(props) {

    const handleClick = e =>
    {
        console.log(e.target.id)
        if(document.getElementById(e.target.id) !== null){
            if(document.getElementById(e.target.id).style.textDecoration === "line-through"){
            document.getElementById(e.target.id).style.textDecoration = "none";
            document.getElementById(e.target.id).style.opacity = "1";

            }else{
                document.getElementById(e.target.id).style.textDecoration = "line-through";
                document.getElementById(e.target.id).style.opacity = ".3";  
            }
            
        }
        
    }

    console.log("AA:", props.data)
    return (
        <div>
        <ul>
            {props.data.map(data=>{
                return(
                    <li id={data.alias} style={{textAlign: "left"}} onClick={handleClick} >
                        <h2>{data.alias}<span style={{fontSize:"10px", fontWeight:"normal"}}>({data.f1})</span></h2>
                        {data.f2}
                    </li>
                )
            })}
    </ul>
    <div style={{position:"absolute", top:"0", textAlign:"left"}}>
        <h3>
        How would you rate your:  
        </h3>
            <h5>
            Q3 - Information Visualization skills?
            </h5>
            <h5>
            Q4 - Statistical skills?
            </h5>
            <h5>
            Q5 - Mathematics skills?
            </h5>
            <h5>
            Q6 - Drawing and artistic skills?
            </h5>
            <h5>
            Q7 - Computer usage skills?
            </h5>
            <h5>
            Q8 - Programming skills?
            </h5>
            <h5>
            Q9 - Computer graphics programming skills?
            </h5>
            <h5>
            Q10 - Human-computer interaction programming skills?
            </h5>
            <h5>
            Q11 - User experience evaluation skills?
            </h5>
            <h5>
            Q12 - Communication skills?
            </h5>
            <h5>
            Q13 - Collaboration skills?
            </h5>
            <h5>
            Q14 - Code repository skills?
            </h5>
  
    </div>
    </div>
    )
}

